*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins',sans-serif;
} 
 
body{
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    background: #f0f8ff;
    font-family: Arial;
  }
 
.container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px
}
img{
    width: 100%;
     
}
 
.container .card{
  position: relative;
  width: 370px;
  height: 380px;
  border-radius: 10px;
  box-shadow: 0px 15px 25px rgba(0,0,0,.2);
  background-color: #fff;
  margin: 20px;
  overflow: hidden;
  transition: .5s;
}
 
.container:hover .card{
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  opacity: 0.5;
} 
 
.container .card:hover{
  -webkit-filter: blur(0);
          filter: blur(0);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  opacity: 1;
} 
 
.container .card .circle{
  position: relative;
  margin-top: 20px;
  width: 350px;
  height: 175px;
  background-color: white;
  text-align: center;
  margin-left: 7px;
}

 
 
.container .card .content{
  position: absolute;
  bottom: 0px;
  page-break-after: 20px;
  text-align: center;
  padding: 20px;
}
 
.container .card .content p{
  color: #666;
  text-align: justify;
  font-size: 15px;
}
 
.container .card .content a{
  position: relative;
  display: inline-block;
  background-color: #000;
  color: #fff;
  padding: 10px 20px;
  border-radius: 40px;
  margin-top: 20px;
  text-decoration: none;  
}
 
.container .card:nth-child .circle,
.container .card:nth-child .content a{
  background-color: white;
}
 
